import i18next from 'i18next';
import { currentLang } from '@/i18n';
import msgCN from './cn.json';
import msgEN from './en.json';


const htableI18n = i18next.createInstance();

htableI18n.init({
  lng: currentLang,
  resources: {
    cn: { translation: msgCN },
    en: { translation: msgEN },
  },
});

export { htableI18n as i18n, i18next };
